.cartDiv {
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items: flex-end;
}

 .cartButton {
  background-color: white;
  width: 160px;
  height: 50px;
  border: none;
  box-shadow: 1px 1px 5px;
  font-size: 1.25rem;
}

.cartButton:active {
  box-shadow:inset 1px 1px;
  scale: 98%;
}

.cartButton:hover {
  background-color: rgb(122, 255, 237);
}

.cartBox {
  position: absolute;
  z-index: 1;
  top: 270px;
  background-color: azure;
  width: 400px;
  min-height: 650px;
  box-shadow: black 2px 2px 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.checkoutButton {
  background-color: white;
  width: 160px;
  height: 50px;
  border: none;
  box-shadow: 1px 1px 5px;
  font-size: 1.25rem;
  position: relative;
  left: 20px;
  bottom: 20px;
  }

.checkoutButton:active {
  box-shadow:inset 1px 1px;
  scale: 98%;
}

.checkoutButton:hover {
  background-color: rgb(122, 255, 237);
}

.noItems {
  font-size: 1.25rem;
}